@charset "UTF-8";
body,
html {
  width: 100%;
  height: 100%;
  position: relative; }

.radio-wrap,
.check-wrap {
  display: inline-block;
  line-height: 20px; }

.radio-box,
.check-box {
  display: inline-block;
  margin-right: 15px; }

.radio-box input[type='radio'] {
  display: none; }
  .radio-box input[type='radio'] + i {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(../img/radio.png);
    margin-right: 5px;
    vertical-align: top; }
  .radio-box input[type='radio']:checked + i {
    background: url(../img/radio_active.png); }

.check-box input[type='checkbox'] {
  display: none; }
  .check-box input[type='checkbox'] + i {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(../img/check.png);
    margin-right: 5px;
    vertical-align: top; }
  .check-box input[type='checkbox']:checked + i {
    background: url(../img/check_active.png); }

::placeholder {
  color: #999; }

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #f5f5f5; }

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #f5f5f5; }

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #555; }

